import React from 'react'
import PropTypes from 'prop-types'
import './Price.css'

const Price = (props) => {
    let { value, className } = props
    let classes = ['Price']
    if (className) classes.push(className)

    const emoji = k => <span key={k} role="img" aria-label="flying money emoji"></span>

    let price = value && value.split('').map((p, k) => emoji(k))

    return (
        <div className={classes.join(' ')}>
            <h3>Price</h3>
            <p>{price}</p>
        </div>
    )
}

Price.propTypes = {
    value: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]),
    className: PropTypes.string
}

export default Price


