import React from 'react'
import './Button.css'

const button = (props) => {
    let {
        type,
        style,
        className,
        buttonStyle,
        blocked,
        clicked,
        disabled,
        text,
        children
    } = props

    let classes = ['Button', `Button--${(buttonStyle || 'primary')}`]

    if (!!blocked) classes.push('Button--blocked')

    if (className) classes.push(className)


    return (
        <button type={type || 'button'}
            className={classes.join(' ')}
            onClick={clicked}
            disabled={disabled}
            style={style}>
            {text}
            {children}
        </button>
    )
}

export default button


