import React from 'react'
import './LinkButton.css'

const linkbutton = (props) => {
    let {
        url,
        target,
        style,
        buttonStyle,
        className,
        blocked,
        clicked,
        text,
        children,
        rel
    } = props
   
    if(target && target.toLowerCase() === '_blank') rel += 'noopener noreferrer'

    let classes = ['LinkButton', `LinkButton--${(buttonStyle || 'primary')}`]

    if (!!blocked) classes.push('LinkButton--blocked')

    if (className) classes.push(className)

    return (
        <a className={classes.join(' ')}
            style={style}
            rel={rel}
            onClick={clicked}
            href={url}
            target={target}>
                {text}
                {children}
            </a>
    )
}

export default linkbutton



