import React from 'react'
import './Details.css'

const details = (props) => {
    let { children } = props
    return (
        <div className="Details">
            {React.Children.map(children, (child, i) => {
                return React.cloneElement(child, 
                    {
                        ...this.props,
                        className: 'Details__Detail'
                    }
                )
            })}
        </div>
    )
}

export default details


