import React, { Component, Fragment } from 'react'
import { Offline, Online } from "react-detect-offline"
import './App.css';

import Settings from './settings/Settings';

import Location from './utils/Location'
import CitySelection from './components/CitySelection/CitySelection'
import Overlay from './components/Overlay/Overlay'
import Places from './containers/Places/Places'


class App extends Component {

  constructor() {
    super()
    this.state = {
      background: null,
      position: {}
    }
  }

  componentDidMount() {
    this.askCurrentPosition()
  }

  askCurrentPosition() {
    let state
    let currentLocation = new Location()
    currentLocation.getCurrentPosition()
      .then((response) => {
        state = { ...this.state }
        state.position = response
      })
      .then(() => {
        state.show_overlay = false
        this.setState(state)
      })
      .catch(() => {
        state = { ...this.state }
        state.show_overlay = true
        this.setState(state)
      });
  }

  getMapBackgroundImage() {
    if (this.state.position && this.state.position.latitude) {
      let latitude = this.state.position.latitude
      let longitude = this.state.position.longitude
      return `https://maps.googleapis.com/maps/api/staticmap?center=${latitude},${longitude}&size=1600x150&zoom=12&scale=4&key=${Settings.GOOGLE_API_KEY}`
    }
  }

  render() {

    let styles
    let overlay
    let background_image = this.getMapBackgroundImage();

    if (background_image)
      styles = {
        backgroundImage: `url(${background_image})`
      }

    if (this.state.show_overlay)
      overlay = (
        <Overlay>
          <span role="img" aria-label="world map emoji"></span>
          <h1>Oh, I couldn't get your location</h1>
          <h2>Would you mind to allow it? I promisse I won't track you.</h2>
        </Overlay>
      )

    return (
      <Fragment>
        <Online>
          <div className="App">
            <header className="App__header" style={styles}>
              <CitySelection position={this.state.position} />
            </header>
            <section>
              <Places position={this.state.position} />
            </section>
            <footer>
              <p>This app is using <a rel="noopener noreferrer" target="_blank" href="https://www.yelp.com/developers">YELP Api</a> to get all places information.</p>
              <p>This is a pet project from <a href="http://twitter.com/danielsalvagni">@danielsalvagni</a> to play a bit with React and Google and YELP APIs.</p>
            </footer>
          </div>
          {overlay}
        </Online>
        <Offline>
          <Overlay>
            <span role="img" aria-label="world map emoji"></span>
            <h1>Oh, you are offline</h1>
            <h2>Unfortunately, this app isn't supported offline yet.</h2>
          </Overlay>
        </Offline>
      </Fragment>
    );
  }
}

export default App



