import React, { Component } from 'react'
import Place from '../../components/Place/Place'
import Button from '../../components/Button/Button'
import './Places.css'

import Directions from '../../utils/Directions'
import NearbyPlaces from '../../utils/NearbyPlaces'
import PullToRefresh from '../../utils/PullToRefresh'

class Places extends Component {

    constructor() {
        super()
        this.loading_or_loaded_nearby_places = false
        this.all_places = []
        this.already_viewed = []
        this.state = {
            selected: {},
            isLoadingPlaces: true,
            isLoadingDirections: true
        }
            
        this.ptr = PullToRefresh.init({
            mainElement: 'body',
            onRefresh: (done) => { 
                if(!this.loading_or_loaded_nearby_places) return done() 
                this.selectRandonPlace()
                done()
            }
        });
    }

    componentDidUpdate() {
        if(!this.loading_or_loaded_nearby_places)
            this.loadNearbyPlaces()
    }

    loadNearbyPlaces() {
        this.loading_or_loaded_nearby_places = true
        let nearby = new NearbyPlaces(this.props.position);
        nearby.getNearbyPlaces()
            .then((results) => {
                let state = {...this.state}
                state.isLoadingPlaces = false;
                this.setState(state)

                this.all_places = results
                this.selectRandonPlace()
            })
            .catch(() => {
                const state = { ...this.state }
                state.isLoadingPlaces = false
                state.selected.failed = 'failed'
                this.setState(state)
            })
    }

    validateList() {
        if(!this.all_places.length && this.already_viewed.length) {
            this.all_places = [...this.already_viewed]
            this.already_viewed = []
        }
    }

    selectRandonPlace() {
        this.validateList()
        const state = { ...this.state }
        const random = Math.floor(Math.random() * this.all_places.length)
        
        let selected = {...this.all_places[random]}
        this.already_viewed.push(selected)

        state.selected = selected
        state.isLoadingDirections = true
    
        this.all_places.splice(random, 1)

        this.setState(state)
        this.getDistanceAndDuration()
    }

    getDistanceAndDuration() {

        const position = this.props.position
        const selected = {...this.state.selected}
        const location = selected.coordinates
        const origin = `${position.latitude},${position.longitude}`
        const destination = `${location.latitude},${location.longitude}`

        const directions = new Directions()
        directions.getDistanceAndDuration(origin, destination)
            .then((data) => {
                const state = { ...this.state }
                state.isLoadingDirections = false
                state.selected.directions = data
                this.setState(state)
            })
            .catch(() => {
                const state = { ...this.state }
                state.isLoadingDirections = false
                state.selected.directions = 'failed'
                this.setState(state)
            })
        
    }

    render() {
        let main_button
        if(this.props.position && this.props.position.latitude)
            main_button = <Button buttonStyle="secondary" text="Take me somewhere else" clicked={this.selectRandonPlace.bind(this)} blocked="true" />

        return (
            <div className="Places">
                <Place {...this.state.selected} 
                    isLoadingPlaces={this.state.isLoadingPlaces}
                    isLoadingDirections={this.state.isLoadingDirections}
                    position={this.props.position} />
                <div className="Places__Footer">
                    {main_button}
                </div>
            </div>
        );
    }
}

export default Places;



