import React from 'react'
import PropTypes from 'prop-types'
import './Header.css'

const Header = (props) => {
    let {
        image_url,
        categories,
        display_phone,
        phone,
        name,
        isLoadingPlaces
    } = props

    let style = { backgroundImage: `url(${image_url})` }
    let formatted_categories = categories && categories.map((c) => c.title).join(', ')

    let phone_link = (
        <span className="Header__Phone">
            <span role="img" aria-label="telephone emoji"></span> <a href="tel:{phone}">{display_phone}</a>
        </span>
    )
    
    if(!isLoadingPlaces && !display_phone) { phone_link = ''}
    return (
        <div className="Header" style={style}>
            <h1>{name}</h1>
            <h2>{formatted_categories}</h2>
            {phone_link}
        </div>
    )
}

Header.propTypes = {
    image_url: PropTypes.string,
    categories: PropTypes.array,
    display_phone: PropTypes.string,
    phone: PropTypes.string,
    name: PropTypes.string,
    isLoadingPlaces: PropTypes.bool
}

export default Header


